@tailwind base;
@tailwind components;
@tailwind utilities;
@layer components{
  .navBtn{
      @apply hidden h-6 md:inline-flex
      cursor-pointer hover:scale-125
      transition-all duration-150 ease-out
  }
  .banner{
      position: relative;
      height: 400px;
      color: white;
      object-fit: contain;
  }

  .banner_contents{
      margin-left: 30px;
      padding-top: 80px;
      height: 220px;
  }

  .banner_title{
      font-size: 1.5rem;
      font-weight: 800;
      padding-bottom: 0.5rem;
  }

  .banner_description{
      width: 45rem;
      line-height: 1.3;
      padding-top: 1.2rem;
      font-size: 1.1rem;
      max-width: 360px;
      height: 80px;
  }

  .banner--fadeBottom{
    /* top:50px; */
      height: 11.4rem;
      background-image: linear-gradient(
          180deg,
          transparent,
          rgba(37,37,37,0.61),
          #676
      );
  }

  .breadCrumbs--fadeBottom{
    /* top:50px; */
      height: 3.4rem;
      background-image: linear-gradient(
          300deg,
          transparent,
          rgba(37, 37, 37, 0.61),
          #f5f5f5
      );
  }

  .banner_button{
      cursor: pointer;
      color: white;
      outline: none;
      border: none;
      font-weight: 700;
      border-radius: 0.2w;
      padding-left: 2.5rem;
      padding-right: 2.5rem;
      margin-right: 1rem;
      padding-top: 0.5rem;
      padding-bottom: 0.5rem;
      background-color: rgba(51,51,51,0.5);
  }

  .banner_button:hover{
      color: black;
      background-color: #e6e6e6;
      transition: all 0.3s;
  }
  .banner_button_small{
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2w;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    margin-right: 1rem;
    padding-top: 0.3rem;
    padding-bottom: 0.3rem;
    background-color: rgba(51,51,51,0.5);
}
  .link {
    @apply p-2.5 flex rounded-md gap-6 items-center md:cursor-pointer cursor-default duration-300 font-medium;
  }

  .active {
    @apply bg-blue-100 text-blue-600;
  }
}

:root {
  --background: #ffffff;
  --foreground: #171717;
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}

body {
  color: var(--foreground);
  background: var(--background);
  font-family: Arial, Helvetica, sans-serif;
}

/* Remove default margin from body */
body {
  margin: 0;
}

